.card-wallet {
  position: relative !important;
  background-image: url(data:image/png;base64,) !important;
  background-position: center !important;
  background-size: cover !important;
  margin-bottom: 8px !important;
}
.card-wallet .sentre-card-body {
  padding: 16px;
}
.card-wallet::after {
  content: '';
  position: absolute;
  width: calc(100% - 24px);
  height: 100%;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(93, 108, 207, 0.4);
  border-radius: 16px;
  z-index: -1;
}
.card-wallet .balance,
.card-wallet .btn-disconnect {
  color: #e9e9eb !important;
}
.card-wallet .label {
  color: #bec4ec !important;
  font-size: 12px;
}
.card-wallet .icon-sol {
  color: #03e1ff;
}
.card-wallet .text-right {
  text-align: right;
}
.card-wallet .price-arrow {
  background: #7d89d9;
  padding: 0 3px;
  border-radius: 50%;
}
.card-wallet .btn-disconnect {
  background: transparent;
  padding: 0;
  height: auto;
}

.sentre-ribbon-dev {
  left: -4px !important;
  top: 4px !important;
  font-size: 12px !important;
  height: 16px !important;
  padding: 0px 4px !important;
  line-height: 16px !important;
  cursor: pointer !important;
}
.sentre-ribbon-dev .sentre-ribbon-corner {
  left: -2px !important;
  transform: scale(0.5) !important;
}
.sentre-ribbon-dev .sentre-ribbon-text {
  background: transparent !important;
}

#light .droppable-page {
  transition: all 0.25s ease-in-out;
}
#light .droppable-page.active {
  background: #e9e9eb;
  padding: 12px;
  margin: 0px -10px;
  border-radius: 20px;
  min-height: 100px;
}
#light .droppable-page.passive {
  background: transparent;
  border-radius: 0px;
}
#dark .droppable-page {
  transition: all 0.25s ease-in-out;
}
#dark .droppable-page.active {
  background: #373947;
  padding: 12px;
  margin: 0px -10px;
  border-radius: 20px;
  min-height: 100px;
}
#dark .droppable-page.passive {
  background: transparent;
  border-radius: 0px;
}

#light .sentre-active-app .sentre-badge-dot {
  background: #212433;
  box-shadow: 0 0 0 2px #f4f4f5;
}
#dark .sentre-active-app .sentre-badge-dot {
  background: #f4f4f5;
  box-shadow: 0 0 0 2px #212433;
}

.welcome {
  min-height: calc(100vh - 48px);
  margin-top: -40px;
}
.welcome-slide {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}
.welcome-slide .slide-container {
  position: absolute !important;
  width: calc(100% - 96px);
  top: 50%;
  left: 48px;
  transform: translateY(-50%);
  max-height: calc(100vh - 240px);
  overflow: auto;
  -webkit-backdrop-filter: blur(60px);
          backdrop-filter: blur(60px);
}
.welcome-slide .slide-container .sentre-card-body {
  padding: 48px 48px 76px 48px !important;
}
.welcome-slide .slide-container .sentre-card-body .slick-dots {
  bottom: -48px !important;
}
@media (max-width: 768px) {
  .welcome-slide .slide-container .sentre-card-body {
    padding: 16px 16px 48px 16px !important;
  }
  .welcome-slide .slide-container .sentre-card-body .slick-dots {
    bottom: -38px !important;
  }
}
@media (max-width: 768px) {
  .welcome-slide .slide-container {
    width: calc(100% - 48px);
    left: 24px;
  }
}
.welcome-slide .social-button {
  position: absolute !important;
  left: 50%;
  transform: translateX(-50%);
  bottom: 48px;
}
.welcome-slide .social-button button span {
  font-size: 18px !important;
}
.welcome-wallet-connection .card-wallet-connection {
  position: relative;
  width: calc(100% - 96px);
  left: 48px;
  top: 50%;
  transform: translateY(-50%);
}
#light .welcome-slide {
  background-image: url(../../static/media/light-bg-welcome.59d39f87387a25224ddb.png);
}
#light .welcome-slide .slide-container {
  background: linear-gradient(105.88deg, #ffffff7e 0%, #ffe8e972 95.29%) !important;
}
#dark .welcome-slide {
  background-image: url(../../static/media/dark-bg-welcome.37763ea22357ee37f01d.png);
}
#dark .welcome-slide .slide-container {
  background: linear-gradient(105.88deg, #272c4dcc 0%, #99a6f829 95.29%) !important;
}

@media (max-width: 768px) {
  .hero-banner .swiper-pagination {
    bottom: 2px !important;
  }
}
.hero-banner .indicator {
  width: 25px;
  height: 4px;
  border-radius: 4px !important;
  background: #dadada;
  margin: 0 5px;
  border-radius: unset;
  cursor: pointer;
}
@media (max-width: 768px) {
  .hero-banner .indicator {
    width: 18px;
    height: 2px;
    border-radius: 2px !important;
  }
}

